










































@use "~main/theme"

// bootstrap-select has this default
$tsd-dropdown-with: 220px

.tsd-dropdown-toggle
  text-align: left
  width: $tsd-dropdown-with

.tsd-dropdown-menu
  width: $tsd-dropdown-with

.tsd-dropdown-toggle
  color: white
  background-color: theme.$blue
  border-radius: 0px
  border: none
  text-align: left

  &:hover
    color: white
    background-color: theme.$blue

  &:after
    margin-left: 0px

.tsd-dropdown-selected-title
  width: 99%
  float: left

.tsd-dropdown-active-item
  color: white
  background-color: theme.$blue

