.streaming-switch
  background-color: rgba(255, 255, 255, 0.4)
  border: solid thin #888
  position: absolute
  display: flex
  flex-flow: column
  right: 4px
  top: 4px
  z-index: 100

  .btn
    color: #444444
    &.active
      color: #ffffff
      background-color: rgba(0,0,0,0.6)

.menu-icon
  width: 20px
  margin-right: 12px

